import React, { Fragment, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";
import validate from "./validateForm";

const otherField = props => {
	const [hidden, setHidden] = useState(false);
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	return (
		<Fragment>
			{input.name === "pass1" || input.name === "pass2" ? (
				<div className="input-wrapper-div">
					<input
						{...input}
						placeholder={"***********"}
						className={className}
						type={!hidden ? "password" : "text"}
					/>
					<img
						onClick={() => setHidden(!hidden)}
						src={
							hidden
								? "/assets/images/closeEye.svg"
								: "/assets/images/openEye.svg"
						}
						alt="eye svg"
					/>
				</div>
			) : (
				<input
					{...input}
					placeholder={label}
					className={className}
					type={type}
				/>
			)}
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function RegistrationFormMobile(props) {
	const { form } = useSelector(state => state);
	const h = useHistory();
	const { t } = useTranslation();
	const { footer } = useSelector(state => state.menu);
	const menu = footer.find(m => m.slug === "o-kompanii-1");
	const offer = menu.children.find(m => m.slug === "politiki-i-pravila");
	const [disable, setDisable] = useState(true);

	useEffect(() => {
		if (form.registrationFormMobile && form.registrationFormMobile.values) {
			const {
				firstName,
				lastName,
				pass1,
				pass2,
				phoneNumber,
				checkbox
			} = form.registrationFormMobile.values;
			if (
				firstName &&
				lastName &&
				pass1 &&
				pass2 &&
				phoneNumber &&
				checkbox
			) {
				setDisable(false);
			} else setDisable(true);
		}
	}, [form.registrationFormMobile]);

	const { handleSubmit, setLogin, loading } = props;

	return (
		<form className="registration-form form-group" onSubmit={handleSubmit}>
			<h3 className="text-center">{t("registration")}</h3>
			<label htmlFor="register-phonenumber">
				{t("phone-number")} <span>*</span>
			</label>
			<Field
				type="phone"
				className="form-control"
				id="register-phonenumber"
				component={otherField}
				name="phoneNumber"
				required
			/>
			<label htmlFor="register-name">
				{t("name")} <span>*</span>
			</label>
			<Field
				type="text"
				className="form-control"
				id="register-name"
				name="firstName"
				component={otherField}
				required
			/>
			<label htmlFor="register-surname">
				{t("surname")} <span>*</span>
			</label>
			<Field
				type="text"
				className="form-control"
				id="register-surname"
				name="lastName"
				component={otherField}
				required
			/>
			<label htmlFor="register-password">
				{t("pick-pass")} <span>*</span>
			</label>
			<Field
				type="password"
				className="form-control"
				id="register-password"
				component={otherField}
				name="pass1"
				required
			/>
			<label htmlFor="register-password-2">
				{t("repeat-pass_2")} <span>*</span>
			</label>
			<Field
				type="password"
				className="form-control"
				component={otherField}
				id="register-password-2"
				name="pass2"
				required
			/>

			<div
				className="custom-checkbox"
				style={{
					position: "relative"
				}}
			>
				<Field
					type="checkbox"
					name="checkbox"
					className="custom-control-input"
					id="register-policy-2"
					component="input"
					defaultChecked={true}
					defaultValue={true}
					required
				/>
				<label
					className="custom-control-label"
					htmlFor="register-policy-2"
				>
					<p className="mb-0">
						{t("offer_1")}
						<a href={`/page/${offer.slug}`}> {t("offer_2")}</a>
						{t("offer_3")}
					</p>
				</label>
			</div>
			<button
				type="submit"
				className="btn btn-primary"
				disabled={disable || loading}
				style={{ marginTop: "10px", padding: "12px 0", width: "100%" }}
			>
				<span className="mr-2">{t("registration")}</span>
				{loading && <Loader />}
			</button>
			<div className="footer-btns m-0">
				<button className="btn" onClick={() => setLogin(true)}>
					<span className="mr-2">{t("login_2")}</span>
					<img src="/assets/images/icons/arrow.svg" alt="arrow" />
				</button>
				<button onClick={() => h.push("/")} className="btn">
					{t("skip2")}
				</button>
			</div>
		</form>
	);
}

export default reduxForm({
	form: "registrationFormMobile",
	validate
})(RegistrationFormMobile);
