import React, { Fragment } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import countries from "../../../components/features/AllRegions";
import validate from "./validateForm";

const renderField = props => {
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		} else {
			className = className + " correct";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={label}
				className={className}
				type={type}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

const otherField = props => {
	let {
		input,
		label,
		type,
		className,
		minLength,
		meta: { touched, error },
		checked
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={label}
				className={className}
				minLength={minLength}
				type={type}
				checked={checked}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

const selectField = props => {
	let {
		select,
		style,
		className,
		meta: { touched, error },
		checked
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	const uzb = countries.find(v => v.code === "UZ");
	return (
		<Fragment>
			<select
				style={style}
				{...select}
				className={className}
				checked={checked}
			>
				{countries.map((county, i) => {
					return (
						<option
							key={i}
							value={county.name}
							selected={uzb.code === county.code}
						>
							{county.name}
						</option>
					);
				})}
			</select>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function RegistrationFormTwo(props) {
	const { t } = useTranslation();
	const { handleSubmit } = props;

	return (
		<form onSubmit={handleSubmit}>
			<div className="row mt-3">
				<div className="col-12 form-group">
					<label>
						{t("country_or_region")} <span>*</span>
					</label>
					<Field
						type="select"
						style={{
							marginBottom: "0"
						}}
						className="form-control rounded"
						id="region"
						name="region"
						component={selectField}
						required
					/>
				</div>

				<div className="col-6 form-group">
					<label htmlFor="register-name">
						{t("name")} <span>*</span>
					</label>
					<Field
						type="text"
						className="form-control"
						id="register-name"
						name="firstName"
						component={otherField}
						customer
						required
					/>
				</div>
				<div className="col-6 form-group">
					<label htmlFor="register-surname">
						{t("surname")} <span>*</span>
					</label>
					<Field
						type="text"
						className="form-control"
						id="register-surname"
						name="lastName"
						component={otherField}
						required
					/>
				</div>

				<div className="col-12 form-group">
					<label htmlFor="phoneNumber">
						{t("phone-number")} <span>*</span>
					</label>
					<Field
						type="input"
						className="form-control"
						id="phoneNumber"
						component={otherField}
						name="phoneNumber"
						required
					/>
				</div>

				<div className="col-12 form-group">
					<label htmlFor="register-password">
						{t("pick-pass")} <span>*</span>
					</label>
					<Field
						type="password"
						className="form-control"
						id="register-password"
						minLength={6}
						component={otherField}
						name="passwordFirst"
						required
					/>
				</div>

				<div className="col-12 form-group">
					<label htmlFor="register-password-2">
						{t("repeat-pass")} <span>*</span>
					</label>
					<Field
						type="password"
						className="form-control"
						component={renderField}
						id="register-password-2"
						name="passwordSecond"
						required
					/>
				</div>

				<div className="col-12 form-group">
					<label htmlFor="register-email">{t("email")}</label>
					<Field
						type="input"
						className="form-control"
						id="register-email"
						component={otherField}
						name="email"
						required
					/>
				</div>
			</div>

			<div>
				<button
					type="submit"
					className="btn btn-primary"
					style={{
						cursor: "pointer",
						marginTop: "10px",
						width: "100%"
					}}
				>
					<span>{t("continue")}</span>
					<i className="icon-long-arrow-right"></i>
				</button>
			</div>
		</form>
	);
}

export default reduxForm({
	form: "registrationForm",
	initialValues: { tadmin: true },
	validate
})(RegistrationFormTwo);
