import React from "react";
import { setUserForm } from "../../../actions/user";
import { connect } from "react-redux";
import RegistrationFormTwo from "../forms/registrationFormTwo";
import { setPhoneToGetVerifyCode } from "../../../api/verification";

function RegFormTabTwo({ onSuccessSubmit, dispatch, modalData, customer }) {
	const submit = values => {
		dispatch(setUserForm(values));
		setPhoneToGetVerifyCode(values.phoneNumber);
		onSuccessSubmit();
	};
	return (
		<RegistrationFormTwo
			customer={customer}
			modalData={modalData}
			onSubmit={submit}
			initialValues={{ phoneNumber: modalData?.phoneNum || "+998" }}
		/>
	);
}

export default connect(null, null)(RegFormTabTwo);
