import React, { Fragment, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";
import i18n from "../../../i18n";
import { isEmail } from "../../../utils/string";

const validateAuth = values => {
	const errors = {};
	if (!values.username) {
		errors.username = i18n.t("required");
	} else if (!isEmail(values.username)) {
		if (
			!/\+?(998)(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
				values.username
			)
		) {
			errors.username = i18n.t("invalid-phone-number");
		}
	}
	if (!values.password) {
		errors.password = i18n.t("required");
	} else if (values.password.length < 4) {
		errors.password = i18n.t("more4");
	}

	return errors;
};

const otherField = props => {
	const [hidden, setHidden] = useState(false);
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error }
	} = props;
	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	return (
		<Fragment>
			{input.name === "password" ? (
				<div className="input-wrapper-div">
					<input
						{...input}
						placeholder={label}
						className={className}
						type={!hidden ? "password" : "text"}
					/>
					<img
						onClick={() => setHidden(!hidden)}
						src={
							hidden
								? "/assets/images/closeEye.svg"
								: "/assets/images/openEye.svg"
						}
						alt="eye svg"
					/>
				</div>
			) : (
				<input
					{...input}
					placeholder={label}
					className={className}
					type={type}
				/>
			)}
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function AuthFormMobile(props) {
	const { t } = useTranslation();
	const form = useSelector(state => state.form);
	const { handleSubmit, setLogin, loading, setForgetPassword } = props;
	const [disable, setDisable] = useState(true);

	useEffect(() => {
		if (form.signinFormMobile && form.signinFormMobile.values) {
			const { password, username } = form.signinFormMobile.values;
			if (password && username) {
				setDisable(false);
			} else setDisable(true);
		}
	}, [form.signinFormMobile]);

	return (
		<>
			<form className="auth-form" onSubmit={handleSubmit}>
				<h3 className="text-center">{t("login")}</h3>
				<div className="form-group">
					<label htmlFor="username">
						{t("phone-number")} <span>*</span>
					</label>
					<Field
						type="phone"
						className="form-control"
						id="username"
						component={otherField}
						name="username"
						required
					/>
				</div>
				<div className="form-group">
					<label htmlFor="singin-password-2">
						{t("pass")} <span>*</span>
					</label>
					<Field
						type="password"
						className="form-control"
						id="singin-password-2"
						required
						component={otherField}
						name="password"
					/>
				</div>
				{props.error ? (
					<div>
						<span className="error-text">{props.error}</span>
					</div>
				) : null}

				<button
					disabled={disable || loading}
					type="submit"
					className="login-btn btn btn-primary"
					style={{ borderRadius: "8px" }}
				>
					<span className="mr-2">{t("enter")}</span>
					{loading && <Loader />}
				</button>
			</form>
			<div className="footer-btns mt-2">
				<button className="btn" onClick={() => setLogin(false)}>
					{t("registration")}
				</button>
				<button
					onClick={() => setForgetPassword(true)}
					className="btn forgot-link"
				>
					{t("forgot-password")}
				</button>
			</div>
		</>
	);
}

export default reduxForm({ form: "signinFormMobile", validate: validateAuth })(
	AuthFormMobile
);
