import React, { Fragment, useState } from "react";
import { Tabs, TabList, Tab, TabPanel } from "react-tabs";
import VerFormTabTwo from "./formTabs/verFormTabTwo";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import Logo from "../../components/common/Logo";
import ChangeLanguageTwo from "../../components/features/ChangeLanguageTwo";
import RegFormTabTwo from "./formTabs/regFormTabTwo";
import AuthFormTabTwo from "./formTabs/authFormTabTwo";
import { Link } from "react-router-dom";
import LoginMobile from "./LoginMobile";
import { subdomain } from "../../config/market";
import { dt } from "../../i18n";

function Login({ modal, modalData, companyName }) {
	const { t } = useTranslation();
	const [verification, setVerification] = useState(false);
	const [completed, setCompleted] = useState(false);
	const { mainLogo, description, phoneNumbers } = useSelector(
		state => state.marketData.settings
	);
	const [login, setLogin] = useState(true);

	return process.env.REACT_APP_PLATFORM === "mobile" &&
		subdomain === "vibo" ? (
		<LoginMobile />
	) : (
		<div className="login login-two row">
			<div className="col-lg-6 image-side">
				{mainLogo ? (
					<Logo logo={mainLogo} width="140px" height="48px" />
				) : null}
				<div className="login-image">
					<img
						src="/assets/images/vibo/background-login.png"
						alt="background"
					/>
				</div>
				<div className="description d-flex align-items-center justify-content-between">
					<p className="mb-0">{dt(description)}</p>
					<a
						className="d-block"
						style={{
							whiteSpace: "nowrap"
						}}
						href={`tel:${phoneNumbers[0]}`}
					>
						{t("apply")}
					</a>
				</div>
			</div>
			<div className="col-lg-6 form-side">
				<ChangeLanguageTwo />
				{login ? (
					<div className="auth">
						<AuthFormTabTwo login={login} setLogin={setLogin} />
					</div>
				) : (
					<div className="form">
						<h1 className="heading">
							Войдите в{" "}
							<span className="text-primary">{companyName}</span>
						</h1>
						<div className="steps">
							<div className="step active">
								<span>
									{verification ? (
										<i className="icon-check"></i>
									) : (
										1
									)}
								</span>
								<p>{t("information")}</p>
							</div>
							<span
								className={`between-step ${verification &&
									"active"}`}
							></span>
							<div
								className={`step step-second ${verification &&
									"active"}`}
							>
								<span>
									{completed && verification ? (
										<i className="icon-check"></i>
									) : (
										2
									)}
								</span>
								<p>{t("confirmation")}</p>
							</div>
							<span
								className={`between-step ${completed &&
									verification &&
									"active"}`}
							></span>
							<div
								className={`step step-third ${completed &&
									verification &&
									"active"}`}
							>
								<span>
									{completed && verification ? (
										<i className="icon-check"></i>
									) : (
										3
									)}
								</span>
								<p>{t("finished")}</p>
							</div>
						</div>
						<div className="form-box" style={{ boxShadow: "none" }}>
							<div className="form-tab">
								<Tabs
									defaultIndex={modal === "login" ? 0 : 1}
									selectedTabClassName="show"
								>
									<TabList
										className={`nav nav-pills nav-fill ${(verification ||
											completed) &&
											"d-none"}`}
									>
										{completed ? (
											<Tab className="nav-item"></Tab>
										) : verification ? (
											<Tab className="nav-item"></Tab>
										) : (
											<Fragment>
												<a
													href="https://app.ox-sys.com/sign-up"
													className="nav-item"
													target="blank"
												>
													<span className="nav-link">
														{t("imsupplier")}
													</span>
												</a>

												<Tab className="nav-item">
													<span className="nav-link">
														{t("customer")}
													</span>
												</Tab>
											</Fragment>
										)}
									</TabList>
									<div className="tab-content">
										{completed ? (
											<TabPanel
												style={{
													paddingTop: "2rem"
												}}
											>
												<div className="completed">
													{t(
														"registration-completed"
													)}
												</div>
												<Link
													to="/profile"
													className="continue btn btn-primary w-100"
												>
													{t("enter")}
												</Link>
											</TabPanel>
										) : verification ? (
											<TabPanel
												style={{
													paddingTop: "2rem"
												}}
											>
												<VerFormTabTwo
													setCompleted={setCompleted}
												/>
											</TabPanel>
										) : (
											<Fragment>
												<TabPanel>
													<RegFormTabTwo
														customer={true}
														modalData={modalData}
														onSuccessSubmit={() => {
															setVerification(
																true
															);
														}}
													/>
												</TabPanel>
											</Fragment>
										)}
									</div>
								</Tabs>
								<button
									className="btn mt-2 d-block ml-auto"
									onClick={() => setLogin(!login)}
								>
									{!login ? t("login") : t("registration")}
								</button>
							</div>
						</div>
					</div>
				)}
			</div>
		</div>
	);
}

export default React.memo(Login);
