import React, { useState } from "react";
import { setUserData } from "../../../actions/user";
import { syncCarts } from "../../../actions/cart";
import { connect } from "react-redux";
import AuthForm from "../forms/AuthForm";
import Loader from "../../../components/features/loaders/loader";
import {
	authUser,
	getUserByToken,
	requestPassword,
	requestPasswordChange
} from "../../../api/user";
import { closeModal } from "../../../actions/modal";
import { SubmissionError } from "redux-form";
import { isEmail } from "../../../utils/string";
import { useHistory } from "react-router-dom";
// import { useTranslation } from "react-i18next";
import ForgetPassword from "../forms/ForgetPassword";
import NewPassword from "../forms/NewPassword";
import i18n from "../../../i18n";

const validate = values => {
	const errors = {};
	if (!values.password) {
		errors.password = "Required";
	} else if (values.password.length < 4) {
		errors.password = "Must be 5 characters minimum";
	}

	if (!values.username) {
		errors.username = "Required";
	} else if (!isEmail(values.username)) {
		if (
			!/\+?(998)(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
				values.username
			)
		) {
			errors.username = "Is not correct number";
		}
	}
	if (errors.password || errors.username) {
		throw new SubmissionError({
			...errors,
			_error: "Неверный логин!"
		});
	}
};

const validataNumber = values => {
	const errors = {};
	if (!values.phoneNumber) {
		errors.phoneNumber = "Required";
	} else if (
		!/\+?(998)(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
			values.phoneNumber
		)
	) {
		errors.phoneNumber = "Is not correct number";
	}
	if (errors.phoneNumber) {
		throw new SubmissionError({
			...errors
		});
	}
};

const validateCodeAndPasswords = values => {
	const errors = {};
	if (!values.code) {
		errors.code = "Required";
	}
	if (!values.pass1) {
		errors.pass1 = "Required";
	}
	if (!values.pass2) {
		errors.pass2 = "Required";
	}
	if (values.code && values.code.length !== 4) {
		errors.code = "Поле должен состоять максимум из 4 цыфри";
	}
	if (values.pass1 && values.pass1.length < 4) {
		errors.pass1 = "Поле должен состоять максимум из 4 цыфри";
	}
	if (values.pass1 !== values.pass2) {
		errors.pass2 = i18n.t("invalid-inf");
	}
	if (errors.code || errors.pass1 || errors.pass2) {
		throw new SubmissionError({
			...errors
		});
	}
};

function corrector(values) {
	let { username } = values;
	username = username.replace(/ /g, "");
	if (!isEmail(username)) {
		username = username.replace(/[+-]/g, "");
	}
	return { ...values, username };
}

function AuthFormTab({ dispatch, login, setLogin }) {
	const [loading, setLoading] = useState(false);
	const [phoneNumber, setPhoneNumber] = useState();
	const [loadingPassword, setLoadingPassword] = useState(false);
	const [open, setOpen] = useState(false);
	const [openPass, setOpenPass] = useState(false);
	const history = useHistory();
	const prevPath = history.location.state?.prevPathname
		? history.location.state?.prevPathname
		: "";

	const submit = async values => {
		try {
			validate(values);
			values = corrector(values);
			setLoading(true);
			const dataToken = await authUser(values);
			const userData = await getUserByToken(dataToken.token).catch(() => {
				setTimeout(() => {
					submit(values);
				}, 1000);
			});
			if (userData) {
				// if (window.location.pathname !== "/cart") {
				// 	history.push("/profile");
				// }
				syncCarts(dataToken.token);
				dispatch(setUserData(userData, dataToken));
				dispatch(closeModal("login"));
				console.log(prevPath);
				history.push(prevPath ? prevPath : "/profile");
				return true;
			}
		} catch (error) {
			setLoading(false);
			throw new SubmissionError({
				_error: "Неверный логин!"
			});
		}
	};

	const forgetPassSubmit = async values => {
		setLoadingPassword(true);
		validataNumber(values);
		requestPassword(values).then(() => {
			setPhoneNumber(values.phoneNumber);
			setOpen(false);
			setLoadingPassword(false);
			setOpenPass(true);
		});
	};

	const newPassSubmit = async values => {
		setLoadingPassword(true);
		validateCodeAndPasswords(values);
		requestPasswordChange(values).then(async () => {
			const dataToken = await authUser({
				password: values.pass1,
				username: phoneNumber
			});
			const userData = await getUserByToken(dataToken.token).catch(() =>
				setTimeout(() => {
					newPassSubmit(values);
				}, 1000)
			);
			if (window.location.pathname !== "/cart") {
				history.push("/profile");
			}
			syncCarts(dataToken.token);
			dispatch(setUserData(userData, dataToken));
			dispatch(closeModal("login"));
		});
	};

	if (loading) {
		return <Loader />;
	}
	return (
		<div>
			<h1 className="heading">
				Вход в <span className="text-primary">Vibo marketplace</span>
			</h1>
			<AuthForm
				onSubmit={submit}
				setOpen={setOpen}
				login={login}
				setLogin={setLogin}
			/>
			<ForgetPassword
				loadingPassword={loadingPassword}
				setLoadingPassword={setLoadingPassword}
				open={open}
				setOpen={setOpen}
				onSubmit={forgetPassSubmit}
			/>
			<NewPassword
				loadingPassword={loadingPassword}
				openPass={openPass}
				onSubmit={newPassSubmit}
			/>
		</div>
	);
}

export default connect(null, null)(AuthFormTab);
