import React from "react";
import { useState, useEffect } from "react";

function TimerButton({ resendTime = 60, onClick }) {
	const [timer, setTimer] = useState(resendTime);
	const [reset, setReset] = useState(false);

	useEffect(() => {
		let interval = setInterval(() => {
			if (timer > 0) {
				setTimer(prev => prev - 1);
			} else {
				clearInterval(interval);
			}
		}, 1000);

		return () => {
			clearInterval(interval);
		};
	}, [reset]);

	return (
		<button
			disabled={timer > 0}
			onClick={e => {
				e.preventDefault();
				onClick();
				setTimer(resendTime);
				setReset(!reset);
			}}
			className={`refresh-button ${timer > 0 && "active"}`}
		>
			<span className="time">
				{timer > 0 ? timer : <i className="icon-refresh"></i>}
			</span>
		</button>
	);
}

export default TimerButton;
