import React from "react";
import { useDispatch, useSelector } from "react-redux";
import VerificationFormTwo from "../forms/verificationFormTwo";
import { setPhoneToGetVerifyCode, verifyCode } from "../../../api/verification";
import { useState } from "react";
import { SubmissionError } from "redux-form";
import { createUser, authUser } from "../../../api/user";
import { setUserData } from "../../../actions/user";
import { useTranslation } from "react-i18next";
import LoaderOverlay from "../../../components/features/loaders/LoaderOverlay";
import { syncCarts } from "../../../actions/cart";
import { useHistory } from "react-router";

function VerFormTabTwo({ setCompleted }) {
	const dispatch = useDispatch();
	const form = useSelector(state => state.user.form);
	const history = useHistory();
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	let timer;
	function onResend() {
		setPhoneToGetVerifyCode(form.phoneNumber);
	}

	const submit = values => {
		values.code =
			values.code1 +
			values.code2 +
			values.code3 +
			values.code4 +
			values.code5;

		return verifyCode(form.phoneNumber, values.code)
			.then(async () => {
				setLoading(true);
				const userData = await createUser(form);
				if (userData) {
					const dataToken = await authUser({
						username: form.phoneNumber,
						password: form.passwordFirst
					}).catch(() => {
						timer = setTimeout(() => {
							submit(values);
						}, 2000);
					});
					if (dataToken) {
						if (window.location.pathname !== "/cart") {
							history.push("/profile");
						}
						syncCarts(dataToken.token);
						dispatch(setUserData(userData, dataToken));
						setLoading(false);
						setCompleted(true);
						clearTimeout(timer);
						return true;
					}
				}
			})
			.catch(err => {
				setLoading(false);
				console.error(err);
				throw new SubmissionError({
					code: t("invalid-pass"),
					_error: t("invalid-login")
				});
			});
	};

	if (loading) {
		return (
			<div>
				<LoaderOverlay />
			</div>
		);
	}
	return (
		<VerificationFormTwo
			onSubmit={submit}
			onResend={onResend}
			phoneNumber={form.phoneNumber}
		/>
	);
}

export default VerFormTabTwo;
