import React, { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";
import i18n from "../../../i18n";

const validateForgetPass = values => {
	const errors = {};
	if (!values.phoneNumber) {
		errors.phoneNumber = i18n.t("required");
	}
	if (
		values.phoneNumber &&
		!/\+?(998)(\s)?\d{2}(\s)?\d{3}(\s)?\d{2}(\s)?\d{2}$/.test(
			values.phoneNumber
		)
	) {
		errors.phoneNumber = i18n.t("incorrect-number");
	}
	if (!values.pass1) {
		errors.pass1 = i18n.t("required");
	}
	// if (!values.pass2) {
	// 	errors.pass2 = i18n.t("required");
	// }
	// if (values.pass1 !== values.pass2) {
	// 	errors.pass2 = i18n.t("invalid-pass");
	// }
	return errors;
};

const otherField = props => {
	const [hidden, setHidden] = useState(false);
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error }
	} = props;
	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	return (
		<Fragment>
			{input.name === "pass1" ? (
				//  || input.name === "pass2"
				<div className="input-wrapper-div">
					<input
						{...input}
						placeholder={label}
						className={className}
						type={!hidden ? "password" : "text"}
					/>
					<img
						onClick={() => setHidden(!hidden)}
						src={
							hidden
								? "/assets/images/closeEye.svg"
								: "/assets/images/openEye.svg"
						}
						alt="eye svg"
					/>
				</div>
			) : (
				<input
					{...input}
					placeholder={label}
					className={className}
					type={type}
				/>
			)}
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function ForgetPasswordMobile({ handleSubmit, loading }) {
	const { t } = useTranslation();

	return (
		<form
			onSubmit={handleSubmit}
			className="forget-password-form form-group w-auto"
		>
			<h3 className="text-center">{t("forgot-password")}</h3>
			<label htmlFor="phoneNumber">
				{t("phone-number")} <span>*</span>
			</label>
			<Field
				type="phone"
				className="form-control"
				id="phoneNumber"
				component={otherField}
				name="phoneNumber"
				required
			/>
			<label htmlFor="pass1">
				{t("new-password2")} <span>*</span>
			</label>
			<Field
				type="password"
				className="form-control"
				id="pass1"
				name="pass1"
				component={otherField}
				required
			/>
			{/* <label htmlFor="pass2">
				{t("new-password-two")} <span>*</span>
			</label>
			<Field
				type="password"
				className="form-control"
				id="pass2"
				name="pass2"
				component={otherField}
				required
			/> */}
			<button
				disabled={loading}
				type="submit"
				style={{
					padding: "12px 0"
				}}
				className="btn btn-primary w-100 mt-2"
			>
				<span className="mr-2">{t("send")}</span>
				{loading && <Loader />}
			</button>
		</form>
	);
}

export default reduxForm({
	form: "forgetPasswordMobile",
	validate: validateForgetPass
})(ForgetPasswordMobile);
