import React, { Fragment, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Field, reduxForm } from "redux-form";
import Loader from "../../../components/features/loaders/loader";
import TimerButtonThree from "../../../components/TimerButtonThree";
import i18n from "../../../i18n";

const required = value =>
	value || typeof value === "number" ? undefined : i18n.t("required");

function changeFocus(e) {
	if (e.target.name === "code0") {
		if (e.target.value.length === 1) {
			e.target.classList.add("active");
			document.getElementsByName("code1")[0].focus();
		}
	}

	if (e.target.name === "code1") {
		if (e.target.value.length === 1) {
			e.target.classList.add("active");
			document.getElementsByName("code2")[0].focus();
		} else {
			document.getElementsByName("code0")[0].focus();
		}
	}

	if (e.target.name === "code2") {
		if (e.target.value.length === 1) {
			e.target.classList.add("active");
			document.getElementsByName("code3")[0].focus();
		} else {
			document.getElementsByName("code1")[0].focus();
		}
	}

	if (e.target.name === "code3") {
		if (e.target.value.length === 1) {
			e.target.classList.add("active");
			if (document.getElementsByName("code4")[0]) {
				document.getElementsByName("code4")[0].focus();
			}
		} else {
			document.getElementsByName("code2")[0].focus();
		}
	}
	if (e.target.name === "code4") {
		if (e.target.value.length === 1) {
			e.target.classList.add("active");
		} else {
			document.getElementsByName("code3")[0].focus();
		}
	}
}

const renderField = props => {
	let {
		input,
		label,
		type,
		className,
		maxLength,
		meta: { touched, error }
	} = props;

	if (touched) if (error) className = className + " warning";

	return (
		<Fragment>
			<input
				maxLength={maxLength}
				{...input}
				placeholder={label}
				className={className}
				type={type}
			/>
		</Fragment>
	);
};

function VerificationFormMobile(props) {
	const { handleSubmit, loading, length } = props;
	const { t } = useTranslation();
	const fields = Array(length).fill(1);
	const form = useSelector(state => state.form);
	const [disable, setDisable] = useState(true);

	useEffect(() => {
		if (form.verificationFormMobile && form.verificationFormMobile.values) {
			const {
				code0,
				code1,
				code2,
				code3,
				code4
			} = form.verificationFormMobile.values;
			if (
				code0 &&
				code1 &&
				code2 &&
				code3 &&
				(length === 5 ? code4 : true)
			) {
				setDisable(false);
			} else setDisable(true);
		}
	}, [form.verificationFormMobile]);

	return (
		<form onSubmit={handleSubmit}>
			<div className="form-group">
				<label htmlFor="code">
					{t("enter-code3")}
					<br />
					<strong>{props.phoneNumber}</strong>
				</label>
				<label style={{ color: "#111", marginTop: "40px" }}>
					{t("confirmation-code")} <span>*</span>
				</label>
				<div className="verify-inputs-wrapper">
					{fields.map((_, index) => {
						return (
							<Field
								key={index}
								type="phone"
								className="form-control"
								id={"code" + index}
								component={renderField}
								name={"code" + index}
								onChange={changeFocus}
								maxLength={1}
								validate={required}
							/>
						);
					})}
				</div>
			</div>
			<div className="resend-code">
				<TimerButtonThree
					resendText={t("resend-code2")}
					onClick={props.onResend}
				/>
			</div>

			<button
				type="submit"
				className="btn btn-primary mt-3"
				disabled={disable || loading}
				style={{
					width: "100%",
					padding: "12px 0"
				}}
			>
				<span className="mr-2">{t("confirmation")}</span>
				{loading && <Loader />}
			</button>
		</form>
	);
}

export default reduxForm({
	form: "verificationFormMobile"
})(VerificationFormMobile);
