import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { SubmissionError } from "redux-form";
import { syncCarts } from "../../actions/cart";
import { closeModal } from "../../actions/modal";
import { setUserData } from "../../actions/user";
import {
	authUser,
	createUser,
	getUserByToken,
	requestPassword,
	requestPasswordChange
} from "../../api/user";
import { setPhoneToGetVerifyCode, verifyCode } from "../../api/verification";
import { isEmail } from "../../utils/string";
import AuthFormMobile from "./forms/AuthFormMobile";
import ForgetPasswordMobile from "./forms/ForgetPasswordMobile";
import RegistrationFormMobile from "./forms/RegistrationFormMobile";
import VerificationFormMobile from "./forms/VerificationFormMobile";

function corrector(values) {
	console.log(values);
	let { username } = values;
	username = username.replace(/ /g, "");
	if (!isEmail(username)) {
		username = username.replace(/[+-]/g, "");
	}
	return { ...values, username };
}

export default function LoginMobile() {
	const dispatch = useDispatch();
	const history = useHistory();
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	const [login, setLogin] = useState(false);
	const [forgetPassword, setForgetPassword] = useState(false);
	const [verify, setVerify] = useState(false);
	const [form, setForm] = useState({});

	useEffect(() => {
		const menu = document.querySelector(
			".mobile_btm_navbar_box.mobile_btm_navbar_box-four"
		);
		menu.setAttribute(
			"style",
			"width: 0; height: 0; overflow: hidden; padding: 0"
		);

		return () => {
			menu.setAttribute("style", "");
		};
	}, []);

	const authFormSubmit = async values => {
		try {
			setLoading(true);
			values = corrector(values);
			const dataToken = await authUser(values);
			const userData = await getUserByToken(dataToken.token);
			if (userData) {
				syncCarts(dataToken.token);
				dispatch(setUserData(userData, dataToken));
				dispatch(closeModal("login"));
				setLoading(false);
				history.push("/profile");
				return true;
			}
		} catch (error) {
			setLoading(false);
			throw new SubmissionError({
				_error: t("invalid-login")
			});
		}
	};

	const registrationFormSubmit = values => {
		setLoading(true);
		setForm(values);
		setPhoneToGetVerifyCode(values.phoneNumber);

		setTimeout(() => {
			setVerify(true);
			setLoading(false);
		}, 1000);
	};

	const forgetPasswordForm = values => {
		setLoading(true);
		requestPassword({ phoneNumber: values.phoneNumber }).then(() => {
			setVerify(true);
			setForm(values);
			setLoading(false);
		});
	};

	function onResend() {
		setPhoneToGetVerifyCode(form.phoneNumber);
	}

	const verifyForm = async values => {
		setLoading(true);
		const code =
			values.code0 +
			values.code1 +
			values.code2 +
			values.code3 +
			(values.code4 || " ");

		if (login) {
			const data = {
				code: code,
				pass1: form.pass1,
				pass2: form.pass2
			};
			requestPasswordChange(data).then(async () => {
				const dataToken = await authUser({
					password: form.pass1,
					username: form.phoneNumber
				});
				const userData = await getUserByToken(dataToken.token);

				history.push("/profile");

				syncCarts(dataToken.token);
				dispatch(setUserData(userData, dataToken));
				dispatch(closeModal("login"));
			});
		} else {
			try {
				await verifyCode(form.phoneNumber, code);
				const userData = await createUser({
					...form,
					passwordFirst: form.pass1,
					passwordSecond: form.pass2
				});
				if (userData) {
					const dataToken = await authUser({
						username: form.phoneNumber,
						password: form.pass1
					});
					if (dataToken) {
						// if (window.location.pathname !== "/cart") {
						history.push("/profile");
						// }
						syncCarts(dataToken.token);
						dispatch(setUserData(userData, dataToken));
						dispatch(closeModal("login"));
						setLoading(false);
						return true;
					}
				}
			} catch (error) {
				setLoading(false);
				throw new SubmissionError({
					code: t("invalid-pass"),
					_error: t("invalid-login")
				});
			}
		}
	};

	return (
		<div className="login login-two login-mobile">
			<div className="container">
				<img
					className="logo mx-auto"
					src="/assets/images/vibo/preLoaderLogo.svg"
					alt="logo"
					width={"90%"}
				/>
				{verify ? (
					<VerificationFormMobile
						loading={loading}
						onResend={onResend}
						length={login ? 4 : 5}
						phoneNumber={form.phoneNumber}
						onSubmit={verifyForm}
					/>
				) : forgetPassword ? (
					<ForgetPasswordMobile
						loading={loading}
						onSubmit={forgetPasswordForm}
					/>
				) : login ? (
					<AuthFormMobile
						onSubmit={authFormSubmit}
						setLogin={setLogin}
						loading={loading}
						setForgetPassword={setForgetPassword}
					/>
				) : (
					<RegistrationFormMobile
						onSubmit={registrationFormSubmit}
						setLogin={setLogin}
						loading={loading}
					/>
				)}
			</div>
		</div>
	);
}
