// import market from "../../../config/market";

const renderField = props => {
	let {
		input,
		label,
		type,
		className,
		maxLength,
		defaultChecked,
		meta: { touched, error }
	} = props;

	if (touched) if (error) className = className + " warning";

	return (
		<Fragment>
			<input
				maxLength={maxLength}
				{...input}
				placeholder={label}
				className={className}
				defaultChecked={defaultChecked}
				type={type}
			/>

			{touched && error && <span className="error-text">{error}</span>}
		</Fragment>
	);
};

